 @charset 'utf-8';
 .tui-pagination * {
     -moz-user-select: none;
     -webkit-user-select: none;
     -ms-user-select: none;
     user-select: none;
 }
 
 .tui-ico-ellipsis, .tui-ico-first, .tui-ico-last, .tui-ico-next, .tui-ico-prev {
     overflow: hidden;
 }
 
 .tui-pagination {
     height: 40px;
     margin: 14px 0 12px;
     line-height: normal;
     text-align: center;
     font-size: 0;
 }
 
 .tui-pagination .tui-page-btn {
     display: inline-block;
     position: relative;
     width: 40px;
     padding: 13px 0 11px 0;
     margin-left: 0;
     color: #000000;
     font-size: 12px;
     font-weight: normal;
     line-height: 1,333;
     text-decoration: none;
     vertical-align: middle;
     border: none;
     border-width: 1px 0;
     border-radius: 5px;

     &.tui-first, &.tui-last {
        display: none;

        @media screen and (min-width: 768px) {
            display: inline-block;
        }
     }
 }
 
 .tui-pagination .tui-page-btn:hover {
     background-color: #FF6B08;
     color: #fff;
 }
 
 .tui-pagination .tui-is-selected, .tui-pagination strong {
     color: #fff;
     background: #FF6B08;
     border-color: #FF6B08;
     cursor: default;
 }
 
 .tui-pagination .tui-is-selected:hover {
     background-color: #FF6B08;;
     color: #fff;
 }
 
 .tui-pagination .tui-first-child.tui-is-selected, .tui-pagination strong.tui-first-child {
     margin-left: 0
 }
 
